import logo from './logo.svg';
import './App.css';
import React from 'react';
import Cookies from "universal-cookie"

const cookies = new Cookies(); // Fixed typo here, 'Cookies' should start with uppercase 'C'

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      password: "",
      error: "",
      isAuthenticated: false, // Fixed typo here, 'isAuthenticated' should start with uppercase 'I'
    }
  }

  componentDidMount() { // Fixed typo here, 'componentDidMount' should start with lowercase 'c'
    this.getSession();
  }

  getSession = () => {
    fetch("/accounts/session", {
      credentials: "same-origin",
    })
    .then((res) => res.json())
    .then((data) => {
      console.log(data);
      if (data.isAuthenticated) {
        this.setState({ isAuthenticated: true });
      } else {
        this.setState({ isAuthenticated: false });
      }
    })
    .catch((err) => {
      console.log(err);
    });
  }

  // Other methods...

  render() {
    if (!this.state.isAuthenticated) {
      return (
        <div className="container mt-3">
          <h1>React Cookie Auth</h1>
          <br />
          <h2> Login</h2>
          <form onSubmit={this.login}>
            <div className='form-group'>
              <label htmlFor='username'>Username</label> {/* Fixed typo here, 'htmlFor' should match the 'id' attribute of input */}
              <input type="text" className='form-control' id='username' />
            </div>
            <div className='form-group'>
              <label htmlFor='password'>Password</label> {/* Fixed typo here, 'htmlFor' should match the 'id' attribute of input */}
              <input
                type="password"
                className='form-control'
                id='password'
                name='password'
                value={this.state.password}
                onChange={this.handelPasswordChange} // Typo in method name, should be 'handlePasswordChange'
              />
              <div>
                {this.state.error && <small className='text-danger'>{this.state.error}</small>}
              </div>
            </div>
            <button type="submit" className="btn btn-primary">Login</button> {/* Fixed typo here, 'button.btm' should be 'button' with appropriate classes */}
          </form>
        </div>
      );
    }
    return (
      <div className='container-mt-3'>
        <h1> React Cookie Auth</h1>
        <p1> you are logged in</p1>
        <button className="btn btn-primary-mr-2">whoami</button>
        <button className="btn btn-danger">logout</button>

      </div>
    )
    
  }
}

export default App;
